
@charset "UTF-8";
@import url(../css/github-light.css);
* {
  box-sizing: border-box;
}
html {
  font-size: 100px;
}
body,
html {
  padding: 0;
  margin: 0;
  height: 100%;
}
body {
  font-size: .16rem;
  line-height: 2;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-weight: 300;
  color: #323744;
}
a,
a:hover {
  text-decoration: none;
}
h1 {
  font-size: .3rem;
}
h2 {
  font-size: .26rem;
}
h3 {
  font-size: .22rem;
}
h4, h5, h6 {
  font-size: .2rem;
  font-weight: 500;
  margin: 0;
}
p {
  line-height: 1.5;
}
h1, h2, h3, h4, h5, h6 {
  color: #323744;
  font-weight: 300;
}
.content {
  margin-bottom: 0.8rem;
}
.content > h1 {
    text-align: center;
    margin-bottom: 0.8rem;
}
.content pre {
    padding: 0.15rem;
    margin: .2rem 0;
    background: #f0f5f7;
    overflow-x: auto;
    border-radius: 3px;
    line-height: 1.5;
    font-size: .14rem;
}
.content code {
    background: #f0f5f7;
    padding: .02rem .08rem;
    border-radius: 3px;
    font-size: .14rem;
}
.content .article article {
    text-align: left;
    outline: none;
    font-size: .2rem;
}
.content .article article > h1 {
    display: none;
}
.content .article a.anchor {
    display: none;
}
.content .article a {
    color: #1C89F5;
    text-decoration: none;
}
.content .article a:hover {
      border-bottom: 1px dashed #1C89F5;
}
.content .article article h2,
  .content .article article h3 {
    font-weight: 400;
}
.content .article article h2::before,
    .content .article article h3::before {
      content: '#';
      margin-right: 0.15rem;
      font-weight: 900;
      color: #1C89F5;
      font-size: .3rem;
}
.content .article article h2 {
    margin-left: -0.35rem;
}
.content .article article h3 {
    margin-left: 0;
    padding-left: 0;
    border-left: none;
    color: #323744;
}
.content .article article h3::before {
      font-size: .2rem;
}
.content .article article h4 {
    margin: .2rem 0;
    font-weight: 400;
}
.content strong {
    font-weight: 400;
}
.content blockquote {
    border-left: 5px solid #f0f5f7;
    padding: 0 .3rem;
    color: #909BB5;
    margin: .4rem 0;
    overflow: hidden;
}
.content ol {
    margin: .2rem;
}
.content ul {
    margin: .2rem 0;
    list-style: none;
}
.content ul li::before {
    content: '•';
    color: #1C89F5;
    margin-right: .1rem;
}
.content ul li.task-list-item::before {
    content: '';
}
.comment {
  margin: 0 auto;
}



