
@charset "UTF-8";
* {
  box-sizing: border-box;
}
html {
  font-size: 100px;
}
body,
html {
  padding: 0;
  margin: 0;
  height: 100%;
}
body {
  font-size: .16rem;
  line-height: 2;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-weight: 300;
  color: #323744;
}
a,
a:hover {
  text-decoration: none;
}
h1 {
  font-size: .3rem;
}
h2 {
  font-size: .26rem;
}
h3 {
  font-size: .22rem;
}
h4, h5, h6 {
  font-size: .2rem;
  font-weight: 500;
  margin: 0;
}
p {
  line-height: 1.5;
}
h1, h2, h3, h4, h5, h6 {
  color: #323744;
  font-weight: 300;
}
#app {
  min-height: 100vh;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*padding: .5rem 0;*/
}
.main-menu,
.wrap {
  width: 70%;
  margin: 0 auto;
}
.wrap {
  -ms-flex: 1;
      flex: 1;
  margin-bottom: 0.8rem;
}
.main-menu {
  text-align: center;
  margin-bottom: .8rem;
  font-size: .2rem;
  font-family: 'Josefin Slab', serif;
}
.main-menu a {
    color: #323744;
    padding: 0.1rem 0;
    margin: 0 0.15rem;
}
.main-menu a:hover {
      text-decoration: none;
      color: #1C89F5;
}
.main-menu a.router-link-active {
      color: #1C89F5;
      border-bottom: 0.02rem solid #1C89F5;
}
.logo {
  text-align: center;
  padding: .8rem;
  margin-bottom: .2rem;
}
.logo img {
    width: .5rem;
    height: auto;
}
.logo h1 {
    display: none;
}
.loading {
  text-align: center;
  font-size: .3rem;
  color: rgba(255, 255, 255, 0.2);
  font-weight: 600;
}
footer {
  width: 100%;
  background-image: linear-gradient(-90deg, #3CBEFB 0%, #1C89F5 100%);
  padding: .4rem;
  text-align: center;
  position: relative;
}
footer a {
    color: rgba(255, 255, 255, 0.6);
}
footer canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}



